import axios from "axios";
async function load({ params }) {
  const BASE_URL = "http://localhost:8000";
  console.log("Base url", BASE_URL);
  try {
    const trades = await axios.get(`${BASE_URL}/trades`);
    return {
      trades: trades.data.results
    };
  } catch (e) {
    console.log(e);
  }
}
export {
  load
};
